import { Feature, FeatureList, FeatureStore } from './types/Feature';
import { getFeaturesData } from '@app/storage/local/featuresData';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

let listeners: Array<() => void> = [];

const initialStore: FeatureStore = {
  isInitialized: false,
  featuresList: {} as FeatureList,
  loading: false,
  error: null,
};

let tempGlobalStore: FeatureStore = { ...initialStore };

let tempFeaturesList: FeatureList = {} as FeatureList;

async function getFeatureStore() {
  const { featureStore } = await getLocalStorageData(
    LocalStorageKey.FeatureStore,
  );

  if (!featureStore) {
    return initialStore;
  }

  return featureStore as FeatureStore;
}

async function setFeatureStore(store: FeatureStore) {
  await setLocalStorageData({
    [LocalStorageKey.FeatureStore]: store,
  });

  tempGlobalStore = {
    ...store,
    featuresList: store.featuresList,
  };
}

const featureStore = {
  async setFeatureValues(features: Partial<FeatureList>) {
    tempFeaturesList = {
      ...tempFeaturesList,
      ...features,
    };

    tempGlobalStore = {
      ...tempGlobalStore,
      featuresList: {
        ...tempGlobalStore.featuresList,
        ...tempFeaturesList,
      },
    };

    emitChange();

    await setLocalStorageData({
      [LocalStorageKey.FeaturesListLocal]: tempFeaturesList,
    });
  },

  subscribe(listener: () => void) {
    if (!tempGlobalStore.isInitialized && !tempGlobalStore.loading) {
      featureStore.init();
    }

    listeners = [...listeners, listener];

    return () => {
      listeners = listeners.filter((l) => l !== listener);
    };
  },

  getSnapshot(): FeatureStore {
    return tempGlobalStore;
  },

  async getSnapshotAsync(): Promise<FeatureStore> {
    if (!tempGlobalStore.isInitialized && !tempGlobalStore.loading) {
      await this.init();
    }

    const store = await getFeatureStore();
    const { featuresListLocal } = await getLocalStorageData(
      LocalStorageKey.FeaturesListLocal,
    );

    return {
      ...store,
      featuresList: {
        ...store.featuresList,
        ...featuresListLocal,
      },
    };
  },

  async init() {
    try {
      const localStore = await getFeatureStore();

      if (!localStore.isInitialized) {
        await setFeatureStore({ ...initialStore, loading: true });
      }

      const features = await getFeaturesData();

      await setFeatureStore({
        ...initialStore,
        isInitialized: true,
        featuresList: {
          [Feature.DevMeta]: features[Feature.DevMeta] || false,
          [Feature.PACLite]: features[Feature.PACLite] || false,
          [Feature.CaptchaCrackerDisabled]:
            features[Feature.CaptchaCrackerDisabled] || false,
          [Feature.OnFailDisabled]: features[Feature.OnFailDisabled] || false,
          [Feature.AdfoxBanner]: features[Feature.AdfoxBanner] || false,
          [Feature.ProxyControlledByOtherExtensionsVisibility]:
            features[Feature.ProxyControlledByOtherExtensionsVisibility] ||
            false,
          [Feature.ProxyAuth]: features[Feature.ProxyAuth] || false,
          [Feature.FreeUsersReview]: features[Feature.FreeUsersReview] || false,
          [Feature.MultiLocalesSupport]:
            features[Feature.MultiLocalesSupport] || false,
          [Feature.GiftAdvertising]:
            features[Feature.GiftAdvertising] || false,
        },
        loading: false,
        error: null,
      });

      const { featuresListLocal } = await getLocalStorageData(
        LocalStorageKey.FeaturesListLocal,
      );

      tempFeaturesList = featuresListLocal as FeatureList;

      tempGlobalStore = {
        ...tempGlobalStore,
        featuresList: {
          ...tempGlobalStore.featuresList,
          ...featuresListLocal,
        },
      };

      emitChange();
      console.warn('Feature store initialized');
    } catch (error) {
      await setFeatureStore({
        ...initialStore,
        loading: false,
        error: error as Error,
      });

      console.error(error);
    }
  },

  async reset() {
    tempFeaturesList = {} as FeatureList;

    await setLocalStorageData({
      [LocalStorageKey.FeaturesListLocal]: tempFeaturesList,
    });

    emitChange();
  },
};

function emitChange() {
  for (let listener of listeners) {
    listener();
  }
}

export { featureStore };
