import LZString from 'lz-string';
import { Alarms } from '@app/alarms/shared';
import {
  getLocalStorageData,
  setLocalStorageData,
  setProxyData,
} from '@app/storage/local/shared';
import { BaseProxyData } from './types/ProxyData';
import { browser_api } from '@app/browser_api';
import { detectBrowser } from '@app/utils/detectBrowser';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

enum PCAReplaceKey {
  Host = '${PROXY_HOST}',
  Port = '${PROXY_PORT}',
  VPNHost = '${PROXY_VPN_HOST}',
  VPNPort = '${PROXY_VPN_PORT}',
}

type GetPacScriptDto = Pick<BaseProxyData, 'host' | 'port'> & {
  pacString: string;
  shouldDecompress?: boolean;
  vpnHost?: string | null;
  vpnPort?: number | null;
};

const getPacScript = ({
  host,
  port,
  pacString,
  shouldDecompress,
  vpnHost,
  vpnPort,
}: GetPacScriptDto): string => {
  const decompressedScript = shouldDecompress
    ? LZString.decompressFromBase64(pacString)
    : pacString;

  let scriptWithReplacements = decompressedScript
    .replaceAll(PCAReplaceKey.Host, host)
    .replaceAll(PCAReplaceKey.Port, `${port}`);

  if (vpnHost && vpnPort) {
    scriptWithReplacements = scriptWithReplacements
      .replaceAll(PCAReplaceKey.VPNHost, vpnHost)
      .replaceAll(PCAReplaceKey.VPNPort, `${vpnPort}`);
  }

  return scriptWithReplacements;
};

export async function setProxy({
  host,
  port,
  pacString,
  shouldDecompress,
  vpnHost,
  vpnPort,
}: GetPacScriptDto): Promise<void> {
  const pacScript = getPacScript({
    host,
    port,
    pacString,
    shouldDecompress,
    vpnHost,
    vpnPort,
  });

  const pacUrl =
    'data:application/x-ns-proxy-autoconfig;base64,' + btoa(pacScript);

  const chromeValue = {
    mode: 'pac_script',
    pacScript: {
      data: pacScript,
    },
  };

  const firefoxValue = {
    proxyType: 'autoConfig',
    autoConfigUrl: pacUrl,
  };

  await browser_api.proxy.settings.set({
    value: detectBrowser() === 'chrome' ? chromeValue : firefoxValue,
    scope: 'regular',
  });

  if (browser_api.runtime.lastError) {
    await setLocalStorageData({ currentState: 'error' });
    console.error(
      'Error setting proxy:',
      browser_api.runtime.lastError.message,
    );
  } else {
    const { connectionCount, targetState } = await getLocalStorageData([
      LocalStorageKey.ConnectionCount,
      LocalStorageKey.TargetState,
    ]);
    const count = connectionCount ?? 0;
    if (targetState === 'disconnected') {
      void browser_api.alarms.create(Alarms.DisableProxy, {
        delayInMinutes: 0,
      });
    } else {
      await setProxyData(host, port);
      await setLocalStorageData({
        currentState: 'connected',
        connectionCount: count + 1,
      });
    }
  }
}

export async function clearProxy(): Promise<void> {
  await browser_api.proxy.settings.clear({ scope: 'regular' });
  const { targetState } = await getLocalStorageData(
    LocalStorageKey.TargetState,
  );
  if (browser_api.runtime.lastError) {
    await setLocalStorageData({ currentState: 'error' });
    console.error(
      'Error clearing proxy settings:',
      browser_api.runtime.lastError.message,
    );
  } else if (targetState === 'connected') {
    browser_api.alarms.create(Alarms.EnableProxy, {
      delayInMinutes: 0,
    });
  } else {
    console.error('clearProxy');
    await setLocalStorageData({ currentState: 'disconnected' });
  }
}
