import { ProxyCredential } from './types/ProxyData';
import { browser_api } from '@app/browser_api';
import type { WebRequest } from 'webextension-polyfill';
import { detectBrowser } from '@app/utils/detectBrowser';

type ProxyAuthListenerType = (
  details: WebRequest.OnAuthRequiredDetailsType,
) => WebRequest.BlockingResponseOrPromiseOrVoid;

// // Important to store the listener in a out of scope variable to reinitialize it
// let prevListener: ProxyAuthListenerType;

export function initUpdateProxyAuth(proxyCredential: ProxyCredential) {
  console.warn('Proxy auth init/update started');

  if (browser_api.webRequest.onAuthRequired.hasListener(proxyAuthListener)) {
    console.warn('Removing old listener');
    browser_api.webRequest.onAuthRequired.removeListener(proxyAuthListener);
  }

  function proxyAuthListener(
    details: Parameters<ProxyAuthListenerType>[0],
  ): ReturnType<ProxyAuthListenerType> {
    if (!details.isProxy || !proxyCredential) return;

    const { challenger } = details;
    const { host, port } = challenger;

    const credentials = proxyCredential[`${host}:${port}`];

    if (credentials?.userName && credentials?.password) {
      const { userName, password } = credentials;

      return {
        authCredentials: {
          username: userName,
          password: password,
        },
      };
    } else {
      console.error(`No credentials found for proxy ${host}:${port}`);
    }
  }

  browser_api.webRequest.onAuthRequired.addListener(
    proxyAuthListener,
    { urls: ['<all_urls>'] },
    detectBrowser() === 'chrome' ? ['asyncBlocking'] : ['blocking'],
  );

  console.warn('Proxy auth init/update finished');
}
