import { httpClient } from './httpClient';

type TrialStatus = 'allowed' | 'disallowed';

type TrialAvailabilityResponse = {
  data: { status: TrialStatus; error: null };
};

export async function checkTrialAvailability(
  apiBaseUrl: string,
): Promise<boolean> {
  const url = `https://${apiBaseUrl}/api/v2/check-trial`;

  try {
    const response = await httpClient
      .post<TrialAvailabilityResponse>(url)
      .json();

    return response.data.status === 'allowed';
  } catch (error) {
    console.error(error);
    return false;
  }
}
