import { updatePacScript } from '@background/pac-script/updatePacScript';
import { fetchProxyData } from '@api/fetchProxyData';
import { fetchPremiumProxyData } from '@api/premium/fetchPremiumProxyData';
import { getChromePremiumAlarm, PremiumAlarms } from '@app/alarms/premium';
import { Alarms } from '@app/alarms/shared';
import {
  getLocalStorageData,
  getSubscriptionInfo,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { getMetaApiData } from '@app/storage/local/metaApiData';
import { type ProxyDataResponse } from './types/ProxyData';
import { browser_api } from '@app/browser_api';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export async function getPremiumProxyData({
  apiBaseUrl,
  deviceId,
  deviceIp,
  onFail,
}: {
  apiBaseUrl: string;
  deviceId: string;
  deviceIp?: string;
  onFail?: boolean;
}): Promise<ProxyDataResponse> {
  const { userId, hasPremium } = await getLocalStorageData([
    LocalStorageKey.UserId,
    LocalStorageKey.HasPremium,
  ]);

  if (userId && hasPremium) {
    const data = await fetchPremiumProxyData({
      apiBaseUrl,
      userId,
      deviceId,
      deviceIp,
      onFail,
    });

    if ('error' in data) {
      if (data.error === 'changeDevice') {
        await setLocalStorageData({ isChangeDeviceError: true });
        await setLocalStorageData({ targetState: 'disconnected' });
      }
      throw new Error(data.error);
    }

    return data;
  }

  return await fetchProxyData({ apiBaseUrl, deviceId, deviceIp, onFail });
}

async function updatePremiumProxyData(
  shouldStartProxy: boolean = true,
): Promise<void> {
  const { status, hasPremium } = await getLocalStorageData([
    LocalStorageKey.Status,
    LocalStorageKey.HasPremium,
  ]);

  if (hasPremium && status === 'Expired') {
    await setLocalStorageData({ targetState: 'disconnected' });
    return;
  }
  if (shouldStartProxy) {
    browser_api.alarms.create(Alarms.EnableProxy, {
      delayInMinutes: 0,
    });
  }
}

async function handleUpdateProxyPremiumDataAlarm(): Promise<void> {
  const shouldStart = await shouldStartProxy();

  await updatePremiumProxyData(shouldStart);
  await updatePacScript();
}

async function createOrUpdatePremiumProxyDataAlarm(): Promise<void> {
  try {
    const { proxyUpdatePeriodPremium, proxyUpdatePeriod } =
      await getMetaApiData();

    const { hasPremium } = await getLocalStorageData(
      LocalStorageKey.HasPremium,
    );

    const updatePeriod = hasPremium
      ? proxyUpdatePeriodPremium
      : proxyUpdatePeriod;

    const updatePremiumProxyAlarm = await getChromePremiumAlarm(
      PremiumAlarms.UpdateProxyPremiumData,
    );

    if (
      updatePremiumProxyAlarm &&
      updatePremiumProxyAlarm.periodInMinutes !== updatePeriod
    ) {
      await browser_api.alarms.clear(PremiumAlarms.UpdateProxyPremiumData);
    }

    if (
      !updatePremiumProxyAlarm ||
      updatePremiumProxyAlarm.periodInMinutes !== updatePeriod
    ) {
      browser_api.alarms.create(PremiumAlarms.UpdateProxyPremiumData, {
        periodInMinutes: updatePeriod,
        delayInMinutes: 0,
      });
    }
  } catch (error) {
    console.error(error);
  }
}

async function shouldStartProxy(): Promise<boolean> {
  const { userId, currentState } = await getLocalStorageData([
    LocalStorageKey.UserId,
    LocalStorageKey.CurrentState,
  ]);
  return Boolean(userId && currentState === 'connected');
}

browser_api.alarms.onAlarm.addListener(async (alarm) => {
  const name = alarm.name as PremiumAlarms;

  if (name === PremiumAlarms.UpdateProxyPremiumData) {
    await handleUpdateProxyPremiumDataAlarm();
  }

  if (name === PremiumAlarms.UpdateSubscriptionInfo) {
    await getSubscriptionInfo();
  }
});

browser_api.storage.local.onChanged.addListener(
  async (changes: { isInitialized?: { newValue: boolean } }) => {
    if (changes.isInitialized?.newValue) {
      await createOrUpdatePremiumProxyDataAlarm();

      const updateSubscriptionInfoAlarm = await getChromePremiumAlarm(
        PremiumAlarms.UpdateSubscriptionInfo,
      );

      const { updateSubscriptionInfoPeriod } = await getMetaApiData();

      if (
        updateSubscriptionInfoAlarm &&
        updateSubscriptionInfoAlarm.periodInMinutes !==
          updateSubscriptionInfoPeriod
      ) {
        await browser_api.alarms.clear(PremiumAlarms.UpdateSubscriptionInfo);
      }

      if (
        !updateSubscriptionInfoAlarm ||
        updateSubscriptionInfoAlarm.periodInMinutes !==
          updateSubscriptionInfoPeriod
      ) {
        await browser_api.alarms.create(PremiumAlarms.UpdateSubscriptionInfo, {
          periodInMinutes: updateSubscriptionInfoPeriod,
          delayInMinutes: 0,
        });
      }
    }
  },
);

browser_api.storage.local.onChanged.addListener(
  async (changes: { hasPremium?: { newValue: boolean } }) => {
    if (changes.hasPremium) {
      await createOrUpdatePremiumProxyDataAlarm();

      const { currentState } = await getLocalStorageData(
        LocalStorageKey.CurrentState,
      );

      if (currentState === 'connected') {
        await updatePremiumProxyData();
      }
    }
  },
);

browser_api.runtime.onStartup.addListener(async () => {
  await handleUpdateProxyPremiumDataAlarm();
});

browser_api.runtime.onInstalled.addListener(async () => {
  await handleUpdateProxyPremiumDataAlarm();
});
