import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { backgroundScope } from '@background/shared';
import { handleActiveTabWhenProxyControlledByOtherExtensions } from './handleActiveTabWhenProxyControlledByOtherExtensions';
import { handleUpdatedTabWhenProxyControlledByOtherExtensions } from './handleUpdatedTabWhenProxyControlledByOtherExtensions';
import { browser_api } from '@app/browser_api';
import type { Types } from 'webextension-polyfill';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

async function logProxySettings(
  details: Types.SettingOnChangeDetailsType,
): Promise<void> {
  await setLocalStorageData({
    browserProxySettings: {
      levelOfControl: details.levelOfControl,
    },
  });
}

export async function handleProxyControlledByOtherExtensions(
  details: Types.SettingOnChangeDetailsType,
): Promise<void> {
  try {
    await logProxySettings(details);

    const { currentState } = await getLocalStorageData(
      LocalStorageKey.CurrentState,
    );

    if (currentState !== 'connected') {
      return;
    }

    if (details.levelOfControl === 'controlled_by_other_extensions') {
      const window = await browser_api.windows.getCurrent();
      const tabs = await browser_api.tabs.query({
        active: true,
        currentWindow: true,
      });
      const activeTab = tabs.length > 0 ? tabs[0] : undefined;
      backgroundScope.setTags({
        levelOfControl: details.levelOfControl,
      });
      backgroundScope.setExtras({
        levelOfControl: details.levelOfControl,
        activeTab,
      });
      backgroundScope.setLevel('error');
      backgroundScope.captureException(
        new Error('Proxy controlled by other extensions'),
      );

      await setLocalStorageData({
        [LocalStorageKey.IsProxyControlledByOtherExtensions]: true,
      });
      if (window.id) {
        await browser_api.windows.update(window.id, { focused: true });
        await browser_api.action.openPopup({ windowId: window.id });
      } else {
        browser_api.tabs.onActivated.addListener(
          handleActiveTabWhenProxyControlledByOtherExtensions,
        );
        browser_api.tabs.onUpdated.addListener(
          handleUpdatedTabWhenProxyControlledByOtherExtensions,
        );
      }
    } else if (details.levelOfControl === 'controlled_by_this_extension') {
      await setLocalStorageData({
        [LocalStorageKey.IsProxyControlledByOtherExtensions]: false,
        popupWasOpened: false,
      });
    }
  } catch (error) {
    console.warn(error);
  }
}
