import { PaymentStatus } from '@app/storage/local/types/persistentTypes';
import { httpClient } from './httpClient';
import { HTTPError } from 'ky';
import { setLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

type AcceptGiftDto = {
  userId: string;
  deviceId: string;
};

type AcceptGiftResponse = {
  data?: {
    subscription: {
      status: PaymentStatus;
      paidUntil: string;
      daysLeft: number;
      secondsLeft: number;
    };
  };
  error?: null | string;
};

export async function acceptGift(
  url: string,
  { userId, deviceId }: AcceptGiftDto,
): Promise<AcceptGiftResponse> {
  try {
    const response = await httpClient.post(url, {
      json: {
        userId,
        deviceId,
      },
    });

    const data = (await response.json()) as AcceptGiftResponse;

    if (data.error) {
      throw new Error(data.error);
    }

    return data;
  } catch (error) {
    console.error(error, error instanceof HTTPError);
    if (error instanceof HTTPError) {
      const errorData = await error.response.json();

      if (errorData?.errors?.length > 0) {
        const errorMessage = errorData.errors
          .map((error: { description?: string }) => error.description)
          .filter(Boolean)
          .join(', ');

        await setLocalStorageData({
          [LocalStorageKey.ErrorMessage]: errorMessage,
        });
      }

      if (errorData?.error?.description) {
        await setLocalStorageData({
          [LocalStorageKey.ErrorMessage]: errorData.error.description,
        });
      }

      await setLocalStorageData({
        [LocalStorageKey.ErrorMessage]:
          'Что-то пошло не так. Мы уже знаем об этом и скоро все починим.',
      });
    }
    throw error;
  }
}
