import {
  getLocalStorageData,
  setLocalStorageData,
} from '../../app/storage/local/shared';
import { Actions } from '../../app/actions/shared';
import { Alarms } from '../../app/alarms/shared';
import { backgroundScope } from '@background/shared';
import { analytics } from '../analytics/ga';
import { browser_api } from '@app/browser_api';
import type { WebRequest } from 'webextension-polyfill';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

const WEB_REQUEST_URLS = process.env.WEB_REQUEST_URLS?.split(',') || [];

const YOUTUBE_STATUS_CHECK_URL = '.youtube.com/generate_204';
const YOUTUBE_YOUTUBEI_URL = '.youtube.com/youtubei';
const YOUTUBE_API_STATS_URL = '.youtube.com/api/stats';
const GOOGLE_VIDEOPLAYBACK_URL = '.googlevideo.com/videoplayback';
const TINYIMG_URL = 'ytimg.com';

function isErrProxyConnectionFailed(error: string, errorUrl: string): boolean {
  const ERR_PROXY_CONNECTION_FAILED = 'net::ERR_PROXY_CONNECTION_FAILED';
  const URLS = [
    YOUTUBE_STATUS_CHECK_URL,
    YOUTUBE_YOUTUBEI_URL,
    YOUTUBE_API_STATS_URL,
  ];

  const isErrProxyConnectionFailed =
    error === ERR_PROXY_CONNECTION_FAILED &&
    URLS.some((url) => errorUrl.includes(url));

  if (isErrProxyConnectionFailed) {
    backgroundScope.setExtras({
      isErrProxyConnectionFailed,
      error,
      errorUrl,
    });
    backgroundScope.captureException(new Error(error));
    void analytics.trackError(error, 'proxy_connection_failed');
  }

  return isErrProxyConnectionFailed;
}

function isErrConnectionReset(error: string, errorUrl: string): boolean {
  const ERR_CONNECTION_RESET = 'net::ERR_CONNECTION_RESET';
  const URLS = [YOUTUBE_YOUTUBEI_URL, GOOGLE_VIDEOPLAYBACK_URL];

  const isErrConnectionReset =
    error === ERR_CONNECTION_RESET &&
    URLS.some((url) => errorUrl.includes(url));

  if (isErrConnectionReset) {
    backgroundScope.setExtras({
      isErrConnectionReset,
      error,
      errorUrl,
    });
    backgroundScope.captureException(new Error(error));
    void analytics.trackError(error, 'connection_reset');
  }

  return isErrConnectionReset;
}

function isErrTimedOut(error: string, errorUrl: string): boolean {
  const ERR_TIMED_OUT = 'net::ERR_TIMED_OUT';
  const URLS = [YOUTUBE_YOUTUBEI_URL, GOOGLE_VIDEOPLAYBACK_URL];

  const isErrTimedOut =
    error === ERR_TIMED_OUT && URLS.some((url) => errorUrl.includes(url));

  if (isErrTimedOut) {
    backgroundScope.setExtras({
      isErrTimedOut,
      error,
      errorUrl,
    });
    backgroundScope.captureException(new Error(error));
    void analytics.trackError(error, 'connection_timed_out');
  }

  return isErrTimedOut;
}

function isErrConnectionClosed(error: string, errorUrl: string): boolean {
  const ERR_CONNECTION_CLOSED = 'net::ERR_CONNECTION_CLOSED';
  const URLS = [YOUTUBE_YOUTUBEI_URL];

  const isErrConnectionClosed =
    error === ERR_CONNECTION_CLOSED &&
    URLS.some((url) => errorUrl.includes(url));
  if (isErrConnectionClosed) {
    backgroundScope.setExtras({
      isErrConnectionClosed,
      error,
      errorUrl,
    });

    backgroundScope.captureException(new Error(error));
    void analytics.trackError(error, 'connection_closed');
  }
  return isErrConnectionClosed;
}

function isErrUrl(errorUrl: string): boolean {
  const URLS = [
    YOUTUBE_STATUS_CHECK_URL,
    YOUTUBE_YOUTUBEI_URL,
    YOUTUBE_API_STATS_URL,
    GOOGLE_VIDEOPLAYBACK_URL,
  ];
  return URLS.some((url) => errorUrl.includes(url));
}

function isTargetWebRequestError(error: string, url: string): boolean {
  return (
    isErrProxyConnectionFailed(error, url) ||
    isErrConnectionReset(error, url) ||
    isErrTimedOut(error, url) ||
    isErrConnectionClosed(error, url)
  );
}

export function initNetworkConnectionMonitor(): void {
  // Listener for network errors specifically targeting YouTube
  browser_api.webRequest.onErrorOccurred.addListener(
    createWebRequestErrorHandler(),
    {
      urls: WEB_REQUEST_URLS,
    },
  );

  // Listener for successful requests to YouTube
  browser_api.webRequest.onCompleted.addListener(handleWebRequestComplete, {
    urls: WEB_REQUEST_URLS,
  });
}

function isTinyImgError(
  details: WebRequest.OnErrorOccurredDetailsType,
): boolean {
  return details.url.includes(TINYIMG_URL);
}

function createWebRequestErrorHandler() {
  let isRetrying = false;

  return async (details: WebRequest.OnErrorOccurredDetailsType) => {
    backgroundScope.setTag('WebRequestError', details.error);
    backgroundScope.setExtras({
      statusCode: details.statusCode,
      statusLine: details.statusLine,
      type: details.type,
      url: details.url,
      error: details.error,
      initiator: details.initiator,
    });

    const isTinyimgError = isTinyImgError(details);

    backgroundScope.captureException(new Error(details.error));

    if (isTinyimgError) {
      console.warn('--------------------------------');
      console.warn('The tiny img error occurred', isTinyimgError);
      console.warn('--------------------------------');

      const { isLitePAC } = await getLocalStorageData(
        LocalStorageKey.IsLitePAC,
      );

      if (!isLitePAC) {
        return;
      }

      const isAlarmExists = await browser_api.alarms.get(
        Alarms.UpdatePacScriptToFullPAC,
      );

      if (isAlarmExists) {
        await browser_api.alarms.clear(Alarms.UpdatePacScriptToFullPAC);
      }

      browser_api.alarms.create(Alarms.UpdatePacScriptToFullPAC, {
        delayInMinutes: 0,
      });
    }

    const { currentState, isNetworkError, connectRetryFailed } =
      await getLocalStorageData([
        LocalStorageKey.IsNetworkError,
        LocalStorageKey.ConnectRetryFailed,
        LocalStorageKey.CurrentState,
      ]);

    const isConnected = currentState === 'connected';
    const isTargetRequest = isTargetWebRequestError(details.error, details.url);
    const isTargetConnection = isTargetRequest && isConnected;
    const canRetry = !isRetrying && !connectRetryFailed;

    console.warn('--------------------------------');
    console.warn('isTargetConnection', isTargetConnection);
    console.warn('canRetry', canRetry);
    console.warn('--------------------------------');

    if (isTargetConnection && canRetry) {
      isRetrying = true;
      if (!isNetworkError) {
        await setLocalStorageData({ isNetworkError: true });
        await messageConnectionRetryToAllTabs();

        browser_api.alarms.create(Alarms.EnableProxyOnFail, {
          delayInMinutes: 0,
        });

        const tabs = await browser_api.tabs.query({
          url: '*://*.youtube.com/*',
        });

        await Promise.all(
          tabs.map((tab) => {
            if (tab.id) {
              console.warn('reloading tab', tab.id);

              return browser_api.tabs.reload(tab.id);
            }
            return null;
          }),
        );
      } else {
        await messageConnectionFailToAllTabs();
        await setLocalStorageData({ connectRetryFailed: true });
      }
      isRetrying = false;
    }
  };
}

async function handleWebRequestComplete(
  details: WebRequest.OnCompletedDetailsType,
): Promise<void> {
  const { isNetworkError } = await getLocalStorageData(
    LocalStorageKey.IsNetworkError,
  );

  if (details.statusCode >= 400 || (isErrUrl(details.url) && isNetworkError)) {
    backgroundScope.setTag('webRequestComplete', details.statusCode);
    backgroundScope.setExtras({
      statusCode: details.statusCode,
      statusLine: details.statusLine,
      type: details.type,
      url: details.url,
      initiator: details.initiator,
    });
    backgroundScope.captureException(new Error(details.statusLine));
  }
  if (isErrUrl(details.url) && isNetworkError) {
    await setLocalStorageData({
      isNetworkError: false,
      connectRetryFailed: false,
    });
    await messageConnectionSuccessToAllTabs();
  }
}

async function messageConnectionRetryToAllTabs(): Promise<void> {
  const tabs = await browser_api.tabs.query({ url: '*://*.youtube.com/*' });
  await Promise.all([
    tabs.map((tab) => {
      if (tab.id) {
        return browser_api.tabs.sendMessage(tab.id, {
          action: Actions.NetworkConnectionRetry,
        });
      }
      return null;
    }),
  ]);
}

async function messageConnectionFailToAllTabs(): Promise<void> {
  const tabs = await browser_api.tabs.query({ url: '*://*.youtube.com/*' });
  await Promise.all([
    tabs.map((tab) => {
      if (tab.id) {
        return browser_api.tabs.sendMessage(tab.id, {
          action: Actions.NetworkConnectionRetryFail,
        });
      }
      return null;
    }),
  ]);
}

async function messageConnectionSuccessToAllTabs(): Promise<void> {
  const tabs = await browser_api.tabs.query({ url: '*://*.youtube.com/*' });
  await Promise.all([
    tabs.map((tab) => {
      if (tab.id) {
        return browser_api.tabs.sendMessage(tab.id, {
          action: Actions.NetworkConnectionRetrySuccess,
        });
      }
      return null;
    }),
  ]);
}
