import { ExpirableStorageKey } from '@app/storage/local/types/expirableTypes';
import { OneDayStorageKeys } from '@app/storage/local/types/oneDayTypes';
import { deleteLocalStorageData } from '@app/storage/local/shared';

export async function clearSessionLikeData() {
  const expirableKeys = Object.values(ExpirableStorageKey);
  const oneDayKeys = Object.values(OneDayStorageKeys);
  const keysToRemove = [...expirableKeys, ...oneDayKeys];

  if (keysToRemove.length > 0) {
    await deleteLocalStorageData(keysToRemove);
  }
}
