import { OneDayStorageKeys } from '@app/storage/local/types/oneDayTypes';
import { browser_api } from '@app/browser_api';
import { deleteLocalStorageData } from '@app/storage/local/shared';
import { Alarms } from '@app/alarms/shared';

export async function setupOneDayDataCleaner() {
  browser_api.alarms.create(Alarms.CleanOneDayData, {
    delayInMinutes: 24 * 60,
    periodInMinutes: 24 * 60,
  });

  browser_api.alarms.onAlarm.addListener((alarm) => {
    if (alarm.name === Alarms.CleanOneDayData) {
      const oneDayKeys = Object.values(OneDayStorageKeys);
      deleteLocalStorageData(oneDayKeys);
    }
  });
}
