import type { PremiumActions } from '../actions/premium';

export type InitAdPremiumBannerMessage = {
  action: PremiumActions.InitAdPremiumBanner;
};

export type ClosePremiumAdBannerMessage = {
  action: PremiumActions.CloseAdPremiumBanner;
};

export type OverloadPageReadyMessage = {
  action: PremiumActions.OverloadPageReady;
};

export type OverloadTimerReadyMessage = {
  action: PremiumActions.OverloadTimerReady;
  payload: {
    videoUrl?: string;
  };
};

export type PaymentStartedMessage = {
  action: PremiumActions.PaymentStarted;
  payload: {
    details?: Record<string, string> | null;
  };
};

export type PremiumCanceledMessage = {
  action: PremiumActions.PremiumCanceled;
};

export type PremiumMessage =
  | InitAdPremiumBannerMessage
  | ClosePremiumAdBannerMessage
  | OverloadPageReadyMessage
  | OverloadTimerReadyMessage
  | PaymentStartedMessage
  | PremiumCanceledMessage;

export function isPremiumMessageOfType<T extends PremiumMessage['action']>(
  msg: unknown,
  action: T,
): msg is Extract<PremiumMessage, { action: T }> {
  return (
    typeof msg === 'object' &&
    msg !== null &&
    'action' in msg &&
    (msg as any).action === action
  );
}
