import {
  breadcrumbsIntegration,
  BrowserClient,
  browserTracingIntegration,
  defaultStackParser,
  extraErrorDataIntegration,
  getDefaultIntegrations,
  makeFetchTransport,
  Scope,
} from '@sentry/browser';
import { browser_api } from '@app/browser_api';

export function createSentryScope(name: string) {
  // Check if we're in a browser context (not service worker)
  const isBrowserContext = typeof document !== 'undefined';

  const integrations = isBrowserContext
    ? [
        ...getDefaultIntegrations({}),
        extraErrorDataIntegration({ depth: 10 }),
        browserTracingIntegration(),
        breadcrumbsIntegration(),
      ]
    : [
        // Minimal integrations for service worker context
        extraErrorDataIntegration({ depth: 10 }),
      ];

  const client = new BrowserClient({
    dsn: process.env.SENTRY_DSN,
    environment: process.env.NODE_ENV,
    transport: makeFetchTransport,
    stackParser: defaultStackParser,
    integrations: integrations,
    ignoreErrors: [],
  });

  const scope = new Scope();

  scope.setClient(client);

  scope.setTag('extension_code', process.env.EXTENSION_CODE);
  scope.setTag('environment', process.env.NODE_ENV);
  scope.setTag('service', name);
  scope.setTag('version', browser_api.runtime.getManifest().version);

  client.init();

  self.addEventListener('error', (error) => {
    scope.setExtras({
      type: error.type,
      message: error.message,
      currentTarget: error.currentTarget,
      target: error.target,
      error,
    });
    scope.captureException(error);
  });

  self.addEventListener('unhandledrejection', (error) => {
    scope.setExtras({
      type: error.type,
      reason: error.reason,
      promise: error.promise,
      currentTarget: error.currentTarget,
      target: error.target,
      error,
    });
    scope.captureException(error);
  });

  return scope;
}
