type ApiHttpErrors = Record<
  string,
  {
    timestampUtc: string;
    message: string;
    errorData: unknown;
    status: number;
    statusText: string;
    requestData: unknown;
  }
>;

type ApiHttpSuccess = Record<
  string,
  {
    timestampUtc: string;
    status: number;
    statusText: string;
    url: string;
  }
>;

export enum OneDayStorageKeys {
  ApiHttpErrors = 'apiHttpErrors',
  ApiHttpSuccess = 'apiHttpSuccess',
}

export type OneDayData = {
  [OneDayStorageKeys.ApiHttpErrors]?: ApiHttpErrors;
  [OneDayStorageKeys.ApiHttpSuccess]?: ApiHttpSuccess;
};
