import { HTTPError } from 'ky';
import { httpClient } from '../httpClient';
import { getProxyApiUrl } from '../urls';
import { PremiumProxyDataResponse } from '@background/network/proxy/types/ProxyData';
import { featureStore } from '@app/features/list';
import { Feature } from '@app/features/types/Feature';

type FetchPremiumProxyDataHTTPError = {
  errors: [
    {
      code: 'change_device';
    },
    {
      code: 'subscription_expired';
    },
  ];
};

type PremiumProxyDataError = {
  error: 'changeDevice' | 'internal' | 'unknown';
};

export async function fetchPremiumProxyData({
  apiBaseUrl,
  deviceId,
  deviceIp,
  userId,
  onFail,
}: {
  apiBaseUrl: string;
  deviceId: string;
  deviceIp?: string;
  userId: string;
  onFail?: boolean;
}): Promise<PremiumProxyDataResponse | PremiumProxyDataError> {
  try {
    const proxyApiUrl = getProxyApiUrl(apiBaseUrl);
    const { featuresList } = await featureStore.getSnapshotAsync();

    const apiServerData = await httpClient
      .post<PremiumProxyDataResponse>(proxyApiUrl, {
        json: {
          userId,
          deviceId,
          deviceIp,
          onFail,
          with_auth: featuresList[Feature.ProxyAuth],
        },
      })
      .json();
    return apiServerData;
  } catch (error) {
    console.error(error);
    if (error instanceof HTTPError) {
      const errorData =
        await error.response.json<FetchPremiumProxyDataHTTPError>();
      if (errorData.errors.find((err) => err.code === 'change_device')) {
        return { error: 'changeDevice' };
      }

      if (error.response.status === 500) {
        return { error: 'internal' };
      }
    }
    return { error: 'unknown' };
  }
}
