import { Actions } from '@app/actions/shared';
import { ProxyDataResponse } from './types/ProxyData';
import { setLocalStorageData } from '@app/storage/local/shared';
import { browser_api } from '@app/browser_api';

export async function checkProxyError(proxyData: ProxyDataResponse) {
  if ('errors' in proxyData) {
    const isChangeDeviceError = proxyData.errors?.some(
      (error) => error.code === 'change_device',
    );

    const isProxyNotFoundError = proxyData.errors?.some(
      (error) => error.code === 'proxy_not_found',
    );

    if (isChangeDeviceError) {
      await browser_api.runtime.sendMessage({
        action: Actions.OpenPopupChangeDevice,
      });

      await setLocalStorageData({ isChangeDeviceError: true });
      await setLocalStorageData({ targetState: 'disconnected' });
      return;
    }

    if (isProxyNotFoundError) {
      await setLocalStorageData({ currentState: 'error' });
      return;
    }
  }
}
