import { getPACData } from '@app/storage/local/getPACData';
import { setLocalStorageData } from '@app/storage/local/shared';
import { getMetaApiData } from '@app/storage/local/metaApiData';
import { versionStringToNumber } from '@popup/shared/helpers/versionStringToNumber';

export async function updatePacScript() {
  try {
    const { currentVersion, shouldUpdatePAC, shouldUpdatePACVersion } =
      await getMetaApiData();

    const shouldUpdate = Boolean(
      shouldUpdatePAC === 'true' ||
        (shouldUpdatePACVersion &&
          versionStringToNumber(currentVersion) <=
            versionStringToNumber(shouldUpdatePACVersion)),
    );

    await getPACData({ shouldRefetch: shouldUpdate });
  } catch (err) {
    console.error(err);
    await setLocalStorageData({ currentState: 'error' });
  }
}
