import {
  BaseProxyDataResponse,
  PremiumProxyDataResponse,
  ProxyCredential,
  ProxyDataResponse,
} from './types/ProxyData';
import {
  getLocalStorageData,

} from '@app/storage/local/shared';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export async function getProxyCredentials(proxyData: ProxyDataResponse) {
  function parseAuthString(authString: string) {
    const userName = authString.split(':')[0] || '';
    const password = authString.split(':').slice(1).join(':') || '';
    return { userName, password };
  }

  const result: ProxyCredential = {};

  const { hasPremium } = await getLocalStorageData([
    LocalStorageKey.HasPremium,
  ]);

  if (!hasPremium) {
    const authString =
      (proxyData as BaseProxyDataResponse)?.data?.proxy_server?.auth || '';

    const proxyHost = (proxyData as BaseProxyDataResponse)?.data?.proxy_server
      ?.host;
    const proxyPort = (proxyData as BaseProxyDataResponse)?.data?.proxy_server
      ?.port;

    const { userName, password } = parseAuthString(authString);

    result[`${proxyHost}:${proxyPort}`] = { userName, password };
  } else {
    const { proxies } = proxyData as PremiumProxyDataResponse;

    for (const proxy of proxies) {
      const authString = proxy.auth || '';

      const proxyHost = proxy.host;
      const proxyPort = proxy.port;

      const { userName, password } = parseAuthString(authString);

      result[`${proxyHost}:${proxyPort}`] = { userName, password };
    }
  }

  return result;
}
