import { getConfigsUrl } from 'src/utils';
import { type FeatureListResponse } from '../app/features/types/Feature';
import { configsClient } from './configsClient';

export async function fetchFeaturesData(): Promise<FeatureListResponse> {
  const featuresApiUrl = await getConfigsUrl(process.env.FEATURE_LIST_URL);

  if (!featuresApiUrl) {
    throw new Error('No features API URL provided');
  }

  const data = await configsClient
    .get<FeatureListResponse>(featuresApiUrl.toString())
    .json();

  return data;
}
