async function openExtensionPopup(): Promise<void> {
  try {
    if ('openPopup' in browser_api.action) {
      const window = await browser_api.windows.getCurrent();
      if (window.id) {
        await browser_api.windows.update(window.id, { focused: true });
      }
      await browser_api.action.openPopup({ windowId: window.id });
    }
  } catch (err) {
    console.error('Error opening extension popup:', err);
  }
}

async function setUninstallUrl(userId: string, deviceId: string) {
  const onDeleteUrl = await getConfigsUrl(process.env.ON_DELETE_URL);

  if (onDeleteUrl) {
    if (userId) {
      onDeleteUrl.searchParams.set('clientId', userId);
      onDeleteUrl.searchParams.set('deviceId', deviceId);
    }

    await browser_api.runtime.setUninstallURL(onDeleteUrl.toString());

    if (browser_api.runtime.lastError) {
      console.warn(
        'Failed to set uninstall URL:',
        browser_api.runtime.lastError,
      );
    }
  }
}

async function openExtensionUpdateWelcomePage(
  previousVersion: string,
): Promise<void> {
  try {
    const data = await getMetaApiData();

    const prevVersion = versionStringToNumber(previousVersion);

    const showOnUpdateVersion = versionStringToNumber(
      data.showOnUpdateVersion ?? browser_api.runtime.getManifest().version,
    );

    const [{ apiBaseUrl }, deviceId, deviceIp, { userId, hasPremium }] =
      await Promise.all([
        getMetaApiData(),
        getDeviceId(),
        getDeviceIP(),
        getLocalStorageData([
          LocalStorageKey.UserId,
          LocalStorageKey.HasPremium,
        ]),
      ]);

    const isCorrectVersion = showOnUpdateVersion > prevVersion;
    const isFreeUser = userId && deviceId && !hasPremium;

    if (process.env.ON_UPDATE_URL && isCorrectVersion && isFreeUser) {
      const response = await fetchCheckTariffAvailability(apiBaseUrl, {
        userId,
        deviceId,
        deviceIp,
        tariff: Tariff.FreeTrial,
      });

      if ('error' in response) {
        // TODO: replace error handling https://vpnn.youtrack.cloud/issue/FRO-552
        throw new Error(response.error);
      }
      const { status } = response;

      if (status === 'allowed') {
        const onUpdateUrl = await getConfigsUrl(process.env.ON_UPDATE_URL);

        if (onUpdateUrl) {
          await browser_api.tabs.create({ url: onUpdateUrl.toString() });
        }
      }
    }
  } catch (err) {
    console.error(err);
  }
}

async function checkAlarmTries(name: PremiumAlarms): Promise<void> {
  const { availableAlarmTries } = await getMetaApiData();

  const { alarmTries } = await getLocalStorageData(LocalStorageKey.AlarmTries);

  if (
    !alarmTries ||
    (alarmTries?.[name] && (alarmTries[name] || 0) <= availableAlarmTries)
  ) {
    const tries = alarmTries?.[name] ? (alarmTries[name] || 0) + 1 : 1;

    await setLocalStorageData({ alarmTries: { ...alarmTries, [name]: tries } });
  } else {
    await browser_api.alarms.clear(name);
    await setLocalStorageData({ alarmTries: { ...alarmTries, [name]: 0 } });
  }
}

browser_api.runtime.onInstalled.addListener(async (details) => {
  await clearSessionLikeData();

  if (details.reason === 'install') {
    await getDeviceIP();
    await getDeviceId();

    const { apiBaseUrl } = await getMetaApiData();

    const isTrialAvailable = await checkTrialAvailability(apiBaseUrl);

    const welcomeUrl = isTrialAvailable
      ? await getConfigsUrl(process.env.WELCOME_NEW_URL)
      : await getConfigsUrl(
          process.env.WELCOME_BACK_URL || process.env.WELCOME_URL,
        );

    if (welcomeUrl) {
      await browser_api.tabs.create({ url: welcomeUrl.toString() });
    }

    await openExtensionPopup();
    await analytics.trackUserAction('install', 'extension');
  }

  if (details.reason === 'update') {
    await getDeviceIP();
    await getDeviceId();

    await analytics.trackUserAction(
      'update',
      'extension',
      details.previousVersion ?? '6.1.0',
    );
    await openExtensionUpdateWelcomePage(details.previousVersion ?? '6.1.0');

    const { hasPremium } = await getLocalStorageData(
      LocalStorageKey.HasPremium,
    );

    if (hasPremium) {
      await setLocalStorageData({ isAdBannerShown: false });
    }
  }
});

browser_api.runtime.onMessage.addListener(async (message: unknown) => {
  if (isMessageOfType(message, Actions.OpenPopupRoot)) {
    await openExtensionPopup();
  }

  if (isMessageOfType(message, Actions.OpenPopup)) {
    await openExtensionPopup();

    browser_api.runtime.onMessage.addListener(async function listener(
      msg: unknown,
    ) {
      try {
        const { userId } = await getLocalStorageData(LocalStorageKey.UserId);

        if (isMessageOfType(msg, Actions.PopupReady) && userId) {
          await browser_api.runtime.sendMessage<NavigateToFreeTrialMessage>({
            action: Actions.NavigateToFreeTrial,
          });
          browser_api.runtime.onMessage.removeListener(listener);
        }
      } catch (error) {
        console.error(error);
      }
    });
  }

  if (isMessageOfType(message, Actions.OpenPopupChangeDevice)) {
    // TODO we need to find solution for open popup on change device
  }

  if (isMessageOfType(message, Actions.CreateNewUser)) {
    await createAndInitializeUserWithStatus(UserStatus.New, message.payload);
  }

  if (isMessageOfType(message, Actions.OpenPopupPayment)) {
    await openExtensionPopup();

    browser_api.runtime.onMessage.addListener(async function listener(
      msg: unknown,
    ) {
      if (isMessageOfType(msg, Actions.PopupReady)) {
        await browser_api.runtime.sendMessage<NavigateToPaymentMessage>({
          action: Actions.NavigateToPayment,
        });
        browser_api.runtime.onMessage.removeListener(listener);
      }
    });
  }

  if (isPremiumMessageOfType(message, PremiumActions.InitAdPremiumBanner)) {
    const { hasPremium } = await getLocalStorageData([
      LocalStorageKey.HasPremium,
    ]);

    const isAlarmEnabled = await browser_api.alarms.get(
      PremiumAlarms.ShowAdPremiumBanner,
    );

    if (isAlarmEnabled && hasPremium) {
      await browser_api.alarms.clear(PremiumAlarms.ShowAdPremiumBanner);
      return;
    }

    if (!hasPremium && !isAlarmEnabled) {
      browser_api.alarms.create(PremiumAlarms.ShowAdPremiumBanner, {
        delayInMinutes: 0,
        periodInMinutes: 24 * 60,
      });
    }
  }

  if (isPremiumMessageOfType(message, PremiumActions.CloseAdPremiumBanner)) {
    browser_api.alarms.create(PremiumAlarms.HideAdPremiumBanner, {
      delayInMinutes: 0,
    });
  }

  // This alarm will be stopped when the payment is completed
  if (isPremiumMessageOfType(message, PremiumActions.PaymentStarted)) {
    browser_api.alarms.create(PremiumAlarms.PaymentStarted, {
      periodInMinutes: 0.1,
    });
  }

  if (isPremiumMessageOfType(message, PremiumActions.PremiumCanceled)) {
    browser_api.alarms.create(PremiumAlarms.PremiumCanceled, {
      periodInMinutes: 0.1,
    });
  }
});

browser_api.alarms.onAlarm.addListener(async (alarm) => {
  const name = alarm.name as PremiumAlarms;

  if (name === PremiumAlarms.ShowAdPremiumBanner) {
    const { hasPremium } = await getLocalStorageData(
      LocalStorageKey.HasPremium,
    );

    if (hasPremium) {
      await setLocalStorageData({ isAdBannerShown: false });
    } else {
      await setLocalStorageData({ isAdBannerShown: true });
    }
  }

  if (name === PremiumAlarms.HideAdPremiumBanner) {
    await setLocalStorageData({ isAdBannerShown: false });
    await browser_api.alarms.clear(name);
  }

  if (name === PremiumAlarms.PaymentStarted) {
    const info = await getSubscriptionInfo();
    // We need to check if alarm tries are not exceeded
    await checkAlarmTries(name);

    // We need to check if the payment is completed
    if (info?.data?.subscription?.status === 'Paid') {
      await browser_api.alarms.clear(name);
    }
  }

  if (name === PremiumAlarms.PremiumCanceled) {
    const info = await getSubscriptionInfo();
    // We need to check if alarm tries are not exceeded
    await checkAlarmTries(name);

    // We need to check if the cancel is completed
    if (info?.data?.subscription?.status !== 'Paid') {
      await browser_api.alarms.clear(name);
    }
  }
});

browser_api.runtime.onMessage.addListener(async (message: unknown) => {
  if (isPremiumMessageOfType(message, PremiumActions.InitAdPremiumBanner)) {
    const { hasPremium } = await getLocalStorageData([
      LocalStorageKey.HasPremium,
    ]);

    const isAlarmEnabled = await browser_api.alarms.get(
      PremiumAlarms.ShowAdPremiumBanner,
    );

    if (isAlarmEnabled && hasPremium) {
      await browser_api.alarms.clear(PremiumAlarms.ShowAdPremiumBanner);
      return;
    }

    if (!hasPremium && !isAlarmEnabled) {
      browser_api.alarms.create(PremiumAlarms.ShowAdPremiumBanner, {
        delayInMinutes: 0,
        periodInMinutes: 24 * 60,
      });
    }
  }

  if (isPremiumMessageOfType(message, PremiumActions.CloseAdPremiumBanner)) {
    await browser_api.alarms.create(PremiumAlarms.HideAdPremiumBanner, {
      delayInMinutes: 0,
    });
  }

  // This alarm will be stopped when the payment is completed
  if (isPremiumMessageOfType(message, PremiumActions.PaymentStarted)) {
    await browser_api.alarms.create(PremiumAlarms.PaymentStarted, {
      periodInMinutes: 0.1,
    });
  }

  if (isPremiumMessageOfType(message, PremiumActions.PremiumCanceled)) {
    await browser_api.alarms.create(PremiumAlarms.PremiumCanceled, {
      periodInMinutes: 0.1,
    });
  }
});

browser_api.storage.local.onChanged.addListener(
  async (changes: {
    hasPremium?: { newValue: boolean };
    userId?: { newValue: string };
    deviceId?: { newValue: string };
    configsBaseUrl?: { newValue: string };
  }) => {
    if (changes.hasPremium) {
      await setLocalStorageData({
        isAdBannerShown: !changes.hasPremium.newValue,
      });
    }

    if (changes.userId && changes.userId.newValue) {
      const { deviceId } = await getLocalStorageData(LocalStorageKey.DeviceId);

      if (deviceId) {
        await setUninstallUrl(changes.userId.newValue, deviceId);
      }
    }

    if (changes.deviceId && changes.deviceId.newValue) {
      const { userId } = await getLocalStorageData(LocalStorageKey.UserId);
      if (userId) {
        await setUninstallUrl(userId, changes.deviceId.newValue);
      }
    }

    // we need to reset url when configsBaseUrl is changed
    if (changes.configsBaseUrl && changes.configsBaseUrl.newValue) {
      const { userId, deviceId } = await getLocalStorageData([
        LocalStorageKey.UserId,
        LocalStorageKey.DeviceId,
      ]);

      if (userId && deviceId) {
        await setUninstallUrl(userId, deviceId);
      }
    }
  },
);

browser_api.webRequest.onBeforeRedirect.addListener(
  async (details) => {
    const isCaptchaUrl =
      details.redirectUrl.includes('google.com/sorry') &&
      details.url.includes('youtube.com/watch');

    const shouldSkipCaptcha = await checkShouldSkipCaptcha();

    if (!shouldSkipCaptcha && isCaptchaUrl) {
      const { currentState } = await getLocalStorageData(
        LocalStorageKey.CurrentState,
      );
      if (currentState === 'connected') {
        await redirectToVideo(details.url, details.tabId);
      }
    }
  },
  { urls: ['*://*.youtube.com/*'], types: ['main_frame'] },
  [],
);

async function init(): Promise<void> {
  try {
    const { isBrowserError } = await getLocalStorageData(
      LocalStorageKey.IsBrowserError,
    );
    if (isBrowserError) {
      await analytics.trackError('Browser error detected', 'initialization');
      void setLocalStorageData({
        currentState: 'disconnected',
        targetState: 'disconnected',
      });
    } else {
      await getMetaApiData();

      const shouldAutostart = process.env.SHOULD_AUTOSTART === 'true';

      if (shouldAutostart) {
        await analytics.trackUserAction('autostart', 'proxy');
        await initProxy();
      } else {
        await analytics.trackUserAction('manual_start', 'proxy');
      }

      await setLocalStorageData({ isInitialized: true });

      await initExtensionMonitoring();

      initGiftsOnYoutubeBackground();
    }
  } catch (error) {
    console.error(error);
    await analytics.trackError(
      error instanceof Error ? error.message : 'Unknown error',
      'initialization',
    );
    await setLocalStorageData({ currentState: 'error' });
  }
}

proxyController.fetchProxyData = getPremiumProxyData;

const { isInitialized } = await getLocalStorageData(
  LocalStorageKey.IsInitialized,
);

if (!isInitialized) {
  await init();
}
