import { browser_api } from '@app/browser_api';
import type { Tabs } from 'webextension-polyfill';
import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export async function handleActiveTabWhenProxyControlledByOtherExtensions(activeInfo: {
  tabId: NonNullable<Tabs.Tab['id']>;
}): Promise<void> {
  const tab = await browser_api.tabs.get(activeInfo.tabId);
  const window = await browser_api.windows.getCurrent();

  if (window.id === tab.windowId) {
    const { isProxyControlledByOtherExtensions, popupWasOpened } =
      await getLocalStorageData([
        LocalStorageKey.IsProxyControlledByOtherExtensions,
        LocalStorageKey.PopupWasOpened,
      ]);

    if (isProxyControlledByOtherExtensions && !popupWasOpened) {
      await browser_api.action.openPopup({ windowId: window.id });
    } else {
      browser_api.tabs.onActivated.removeListener(
        handleActiveTabWhenProxyControlledByOtherExtensions,
      );
    }
  }
}
