import { featureStore } from '@app/features/list';
import {
  getLocalStorageData,


} from '@app/storage/local/shared';
import { configsClient } from './configsClient';
import { httpClient } from './httpClient';
import { Feature } from '@app/features/types/Feature';
import { PACRegistryData } from '@app/storage/local/types/persistentTypes';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export async function fetchPACData<T>(registryUrl: string): Promise<T> {
  if (!registryUrl) {
    throw new Error('registryUrl is required');
  }

  const data = await httpClient.get<T>(registryUrl).json();

  return data;
}

export async function fetchPACRegistry(): Promise<PACRegistryData> {
  const { configsBaseUrl } = await getLocalStorageData(
    LocalStorageKey.ConfigsBaseUrl,
  );

  const { featuresList } = await featureStore.getSnapshotAsync();

  const isDevMode = featuresList[Feature.DevMeta];

  const googleRegistryUrl = isDevMode
    ? process.env.PAC_REGISTRY_URL_GOOGLE_TEST
    : process.env.PAC_REGISTRY_URL_GOOGLE;

  const yandexRegistryUrl = isDevMode
    ? process.env.PAC_REGISTRY_URL_YANDEX_TEST
    : process.env.PAC_REGISTRY_URL_YANDEX;

  const isYandex = configsBaseUrl === process.env.CONFIG_BASE_URL_2;

  const pacRegistryFullUrl = isYandex
    ? new URL(yandexRegistryUrl, process.env.CONFIG_BASE_URL_2)
    : new URL(googleRegistryUrl, process.env.CONFIG_BASE_URL_1);

  const data = await configsClient
    .get<PACRegistryData>(pacRegistryFullUrl.toString())
    .json();
  return data;
}
