import { createSentryScope } from '@app/sentry/createSentryScope';
import { CookieKey, getCookieData, setCookieData } from '../cookies/shared';
import { browser_api } from '@app/browser_api';
import { GetSubscriptionResponse } from '@api/urls';
import { getMetaApiData } from '@app/storage/local/metaApiData';
import { getDeviceId, getDeviceIP } from '@background/client/utils';
import {
  fetchSubscriptionInfo,
  SubscriptionInfoError,
} from '@api/premium/fetchPremiumData';
import { Actions } from '@app/actions/shared';
import { getIsPremiumStatus } from '@app/utils/getIsPremiumStatus';
import {
  LocalStorageData,
  LocalStorageKey,
  type LocalStorageKeyType,
} from '@app/storage/local/types/sharedTypes';

export const localStorageScope = createSentryScope('storage');

export async function getLocalStorageData<Key extends LocalStorageKeyType>(
  keys?: Key | Key[],
): Promise<Partial<Pick<LocalStorageData, Key>>> {
  if (!keys) {
    return browser_api.storage.local.get() as Promise<LocalStorageData>;
  }

  return browser_api.storage.local.get(keys) as Promise<
    Pick<LocalStorageData, Key>
  >;
}

export async function setLocalStorageData(
  data: Partial<LocalStorageData>,
): Promise<void> {
  await browser_api.storage.local.set(data);
}

export async function deleteLocalStorageData(
  keys: LocalStorageKeyType | LocalStorageKeyType[],
): Promise<void> {
  await browser_api.storage.local.remove(keys);
}

// We need to update the userId in cookies when it changes in local storage
browser_api.storage.local.onChanged.addListener(async (changes) => {
  if (changes[LocalStorageKey.UserId]) {
    const { userId: cookieUserId } = await getCookieData(CookieKey.UserId);
    const newUserId = changes[LocalStorageKey.UserId].newValue;

    if (typeof newUserId === 'string' && newUserId !== cookieUserId) {
      await setCookieData({
        [CookieKey.UserId]: newUserId,
      });
    }
  }

  if (changes[LocalStorageKey.DeviceId]) {
    const { deviceId: cookieDeviceId } = await getCookieData(
      CookieKey.DeviceId,
    );
    const newDeviceId = changes[LocalStorageKey.DeviceId].newValue;

    if (typeof newDeviceId === 'string' && newDeviceId !== cookieDeviceId) {
      await setCookieData({
        [CookieKey.DeviceId]: newDeviceId,
      });
    }
  }
});

function isGetSubscriptionInfoError(
  data: GetSubscriptionResponse | SubscriptionInfoError,
): data is SubscriptionInfoError {
  return 'error' in data && data.error !== null;
}

export async function setProxyData(
  lastProxyHost: string,
  lastProxyPort: number,
): Promise<void> {
  await setLocalStorageData({ lastProxyHost, lastProxyPort });
}

export async function getSubscriptionInfo(): Promise<GetSubscriptionResponse | null> {
  if (process.env.EXTENSION_CODE === 'naruzhuv2') {
    return null;
  }

  const isOnlyPremium: boolean = process.env.IS_ONLY_PREMIUM === 'true';

  try {
    await setLocalStorageData({ isSubscriptionInfoError: false });

    const { apiBaseUrl } = await getMetaApiData();

    const deviceId = await getDeviceId();
    const deviceIp = await getDeviceIP();

    const { userId } = await getLocalStorageData(LocalStorageKey.UserId);

    if (!userId) {
      localStorageScope.captureException(
        new Error('User ID is empty when fetching subscription info'),
      );
      return null;
    }

    const subscriptionInfo = await fetchSubscriptionInfo(apiBaseUrl, {
      userId,
      deviceId,
      deviceIp,
    });

    if (isGetSubscriptionInfoError(subscriptionInfo)) {
      if (subscriptionInfo.error === 'changeDevice') {
        try {
          await browser_api.runtime.sendMessage({
            action: Actions.OpenPopupChangeDevice,
          });
        } catch (error) {
          console.error(error);
        }

        await setLocalStorageData({ isChangeDeviceError: true });
        await setLocalStorageData({ targetState: 'disconnected' });
        return null;
      }

      if (subscriptionInfo.error === 'requestBlockedError') {
        await setLocalStorageData({
          requestBlockedError:
            'request fetchSubscriptionInfo is probably blocked',
        });
      }

      throw new Error(subscriptionInfo.error);
    }

    const { daysLeft, paidUntil, status, secondsLeft } =
      subscriptionInfo?.data?.subscription ?? {};

    const {
      name: currentTariff,
      deviceLimit,
      activeDevices,
    } = subscriptionInfo?.data?.tariff ?? {};

    const isPremiumStatus = getIsPremiumStatus(status);

    await setLocalStorageData({
      daysLeft,
      paidUntil,
      status,
      secondsLeft,
      currentTariff,
      deviceLimit,
      activeDevices,
    });

    const { hasPremium, currentState } = await getLocalStorageData([
      LocalStorageKey.HasPremium,
      LocalStorageKey.CurrentState,
    ]);

    await setLocalStorageData({
      hasPremium: isOnlyPremium || isPremiumStatus,
    });

    if (status === 'Expired' && hasPremium && currentState === 'connected') {
      await setLocalStorageData({ targetState: 'disconnected' });
    }

    return subscriptionInfo;
  } catch (error) {
    console.error(error);

    if (error instanceof Error && error.message === 'userNotFound') {
      throw error;
    }

    await setLocalStorageData({ isSubscriptionInfoError: true });
    return null;
  }
}
