import {
  CookieKey,
  getCookieData,
  setCookieData,
} from '@app/storage/cookies/shared';
import {
  getLocalStorageData,

} from '@app/storage/local/shared';
import { getDeviceId } from '@background/client/utils';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

export async function moveUserIdAndDeviceIdFromLocalStorageToCookiesForOldUsers() {
  const { userId: localStorageUserId } = await getLocalStorageData(
    LocalStorageKey.UserId,
  );

  const deviceId = await getDeviceId();

  const { userId: cookieUserId } = await getCookieData(CookieKey.UserId);
  const { deviceId: cookieDeviceId } = await getCookieData(CookieKey.DeviceId);

  if (
    localStorageUserId &&
    typeof localStorageUserId === 'string' &&
    !cookieUserId
  ) {
    await setCookieData({ [CookieKey.UserId]: localStorageUserId });
  }

  if (deviceId && typeof deviceId === 'string' && !cookieDeviceId) {
    await setCookieData({ [CookieKey.DeviceId]: deviceId });
  }
}
