import {
  getLocalStorageData,
} from '../../app/storage/local/shared';
import { browser_api } from '@app/browser_api';


import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';

async function updateIcon(): Promise<void> {
  const { currentState, theme } = await getLocalStorageData([
    LocalStorageKey.CurrentState,
    LocalStorageKey.Theme,
  ]);

  // Determine the icon based on the proxy state and theme
  let iconPath;
  if (currentState === 'connected') {
    iconPath = '/icons/icon128.png'; // Proxy is enabled
  } else if (theme === 'light') {
    iconPath = '/icons/icon128-disabled-light.png';
  } else {
    // Default to dark theme if theme is not set or is dark
    iconPath = '/icons/icon128-disabled-dark.png';
  }
  await browser_api.action.setIcon({ path: iconPath });
}

export function initIcon(): void {
  browser_api.storage.onChanged.addListener(
    async (changes: { currentState?: { newValue: string } }) => {
      if (changes.currentState) {
        await updateIcon();
      }
    },
  );
  void updateIcon();
}
