import {
  PersistentStorageData,
  PersistentStorageKey,
} from '@app/storage/local/types/persistentTypes';
import {
  ExpirableData,
  ExpirableStorageKey,
} from '@app/storage/local/types/expirableTypes';
import {
  OneDayData,
  OneDayStorageKeys,
} from '@app/storage/local/types/oneDayTypes';

export type LocalStorageData = PersistentStorageData &
  ExpirableData &
  OneDayData;

export const LocalStorageKey = {
  ...PersistentStorageKey,
  ...ExpirableStorageKey,
  ...OneDayStorageKeys,
} as const;

export type LocalStorageKeyType =
  (typeof LocalStorageKey)[keyof typeof LocalStorageKey];

export type LocalStorageValue<K extends LocalStorageKeyType> =
  LocalStorageData[K];
