import { httpClient } from '../httpClient';
import { UserStatus } from './types/UserStatus';
import type { CreateNewUserResponse } from '../urls';

/**
 * Чтобы создать юзера с флагом is_free_available: true
 * передай в payload параметр - fromTelegram: true
 */

export async function createNewUser({
  url,
  deviceId,
  deviceIp,
  fromTelegram,
}: {
  url: string;
  deviceId: string;
  deviceIp?: string;
  fromTelegram?: boolean;
}): Promise<{ userId: string }> {
  const data = await httpClient
    .post<CreateNewUserResponse>(url, {
      json: { deviceId, deviceIp, fromTelegram },
    })
    .json();
  return data;
}
