import {
  ActionPayloadMap,
  type FailedVideoOpenMessage,
  isMessageOfType,
  type ShowVideoLoaderMessage,
} from '@app/messages/shared';
import { createSentryScope } from '@app/sentry/createSentryScope';
import { getMetaApiData } from '@app/storage/local/metaApiData';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import {
  ConnectionState,
  PaymentStatus,
} from '@app/storage/local/types/persistentTypes';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
