import { moveUserIdAndDeviceIdFromCookiesToLocalStorage } from './user/moveUserIdFromCookiesToLocalStorage';
import { initStore } from '../store/appStore/initStore';

export const backgroundScope = createSentryScope('background');

async function sendMessageToYoutubeTabs<T extends keyof ActionPayloadMap>(
  action: T,
  payload: ActionPayloadMap[T],
): Promise<void> {
  const tabs = await browser_api.tabs.query({ url: ['*://*.youtube.com/*'] });
  for (const tab of tabs) {
    if (tab.id && tab.url) {
      browser_api.tabs.sendMessage(tab.id, { action, payload });
    }
  }
}

(async () => {
  // Функция для очистки "сессионных" данных при каждом запуске
  await clearSessionLikeData();
  // Функция для очистки OneDayStorageKeys каждые 24 часа через alarm
  await setupOneDayDataCleaner();
  // Инициализация стора
  await initStore();
})();

// Storage listeners
browser_api.storage.onChanged.addListener(async (changes, namespace) => {
  if (namespace === 'local' && changes[LocalStorageKey.HasPremium]) {
    const hasPremium = changes[LocalStorageKey.HasPremium].newValue;

    if (typeof hasPremium !== 'boolean') return;

    backgroundScope.setTag('hasPremium', hasPremium);
    backgroundScope.setExtras({
      hasPremium,
      namespace,
    });

    backgroundScope.captureMessage('Has premium changed', 'info');
  }

  if (namespace === 'local' && changes[LocalStorageKey.Status]) {
    sendMessageToYoutubeTabs(Actions.UserStatusChanged, {
      status: changes[LocalStorageKey.Status].newValue as PaymentStatus,
    });
  }

  if (namespace === 'local' && changes[LocalStorageKey.CurrentState]) {
    sendMessageToYoutubeTabs(Actions.ConnectionStateChanged, {
      connectionState: changes[LocalStorageKey.CurrentState]
        .newValue as ConnectionState,
    });
  }

  if (namespace === 'local' && changes[LocalStorageKey.ConfigsBaseUrl]) {
    sendMessageToYoutubeTabs(Actions.ConfigsBaseUrl, {
      configsBaseUrl: changes[LocalStorageKey.ConfigsBaseUrl]
        .newValue as string,
    });
  }
});

// Runtime listeners

browser_api.runtime.onInstalled.addListener(async (details) => {
  try {
    featureStore.init();

    if (details.reason === 'install') {
      backgroundScope.setExtra('details', details);
      backgroundScope.captureMessage('Extension installed', 'info');

      try {
        const { supportedBrowsers } = await getMetaApiData();

        const extensionCode = process.env.EXTENSION_CODE;
        const welcomeUrl = await getConfigsUrl(process.env.WELCOME_URL);

        if (welcomeUrl && extensionCode !== 'youboost-neo') {
          await browser_api.tabs.create({ url: welcomeUrl.toString() });
        }

        const browser = detect();

        const isBrowserSupported =
          browser instanceof BrowserInfo &&
          supportedBrowsers.includes(browser.name);

        if (!isBrowserSupported) {
          await setLocalStorageData({ isBrowserError: true });
          if ('openPopup' in browser_api.action) {
            const window = await browser_api.windows.getCurrent();
            if (window.id) {
              console.warn('onInstalled RELOADING WINDOW');

              await browser_api.windows.update(window.id, { focused: true });
            }
            await browser_api.action.openPopup({ windowId: window.id });
          }
        }

        await updatePacScript();
        await moveUserIdAndDeviceIdFromCookiesToLocalStorage();
      } catch (err) {
        console.error(err);
        await setLocalStorageData({ currentState: 'error' });
      }
    }

    if (details.reason === 'update') {
      backgroundScope.setExtra('details', details);
      backgroundScope.captureMessage('Extension updated', 'info');

      await updatePacScript();

      await moveUserIdAndDeviceIdFromLocalStorageToCookiesForOldUsers();
    }
  } catch (error) {
    console.error(error);
  }
});

browser_api.runtime.onMessage.addListener((async (message, { tab }) => {
  if (isMessageOfType(message, Actions.FailedVideoOpen) && tab?.id) {
    browser_api.tabs.onUpdated.addListener(
      async function listener(tabIdUpdated, changeInfo) {
        if (tabIdUpdated === tab.id && changeInfo.status === 'complete') {
          await browser_api.tabs.sendMessage<FailedVideoOpenMessage>(
            tabIdUpdated,
            message,
          );
          browser_api.tabs.onUpdated.removeListener(listener);
        }
      },
    );
  }

  if (isMessageOfType(message, Actions.SaveTime) && tab?.id) {
    const { time, videoId } = message.payload;

    await setLocalStorageData({
      [LocalStorageKey.SavedTime]: {
        [videoId]: Math.round(time),
      },
    });
  }
}) satisfies Runtime.OnMessageListenerAsync);

browser_api.runtime.onStartup.addListener(async () => {
  backgroundScope.captureMessage('Extension started', 'info');
  await updatePacScript();
});

// Tabs listeners

browser_api.tabs.onActivated.addListener(async (activeInfo) => {
  try {
    const { currentState, status, configsBaseUrl } = await getLocalStorageData([
      LocalStorageKey.CurrentState,
      LocalStorageKey.Status,
      LocalStorageKey.ConfigsBaseUrl,
    ]);

    const tab = await browser_api.tabs.get(activeInfo.tabId);

    if (
      tab.id &&
      tab.url &&
      (tab.url.includes('youtube.com') || tab.url.includes('youtu.be'))
    ) {
      browser_api.tabs.sendMessage(tab.id, {
        action: Actions.UserStatusChanged,
        payload: { status },
      });
      browser_api.tabs.sendMessage(tab.id, {
        action: Actions.ConnectionStateChanged,
        payload: { connectionState: currentState },
      });
      browser_api.tabs.sendMessage(tab.id, {
        action: Actions.ConfigsBaseUrl,
        payload: {
          configsBaseUrl: configsBaseUrl || process.env.CONFIG_BASE_URL_1,
        },
      });
    }

    if (
      tab.id &&
      tab.url &&
      tab.url?.includes('google.com/sorry') &&
      tab.title?.includes('youtube.com/watch')
    ) {
      const shouldSkipCaptcha = await checkShouldSkipCaptcha();

      if (!shouldSkipCaptcha && currentState === 'connected') {
        const newUrl = await replaceTimecodeWithSavedTime(tab.url);

        await browser_api.tabs.update(tab.id, { url: newUrl });
      }
    }
  } catch (error) {
    console.error(error);
  }
});

browser_api.tabs.onUpdated.addListener(async (tabId) => {
  try {
    const tabs = await browser_api.tabs.query({
      active: true,
      currentWindow: true,
    });

    const activeTab = tabs.length > 0 ? tabs[0] : undefined;

    const { currentState, status, configsBaseUrl } = await getLocalStorageData([
      LocalStorageKey.CurrentState,
      LocalStorageKey.Status,
      LocalStorageKey.ConfigsBaseUrl,
    ]);

    const isActiveTab = activeTab?.id === tabId;

    const isConnected = currentState === 'connected';

    if (
      activeTab &&
      activeTab.id &&
      activeTab.url &&
      (activeTab.url.includes('youtube.com') ||
        activeTab.url.includes('youtu.be'))
    ) {
      browser_api.tabs.sendMessage(activeTab.id, {
        action: Actions.UserStatusChanged,
        payload: { status },
      });
      browser_api.tabs.sendMessage(activeTab.id, {
        action: Actions.ConnectionStateChanged,
        payload: { connectionState: currentState },
      });
      browser_api.tabs.sendMessage(activeTab.id, {
        action: Actions.ConfigsBaseUrl,
        payload: {
          configsBaseUrl: configsBaseUrl || process.env.CONFIG_BASE_URL_1,
        },
      });
    }

    const isCaptchaUrl =
      activeTab?.url?.includes('google.com/sorry') &&
      activeTab?.title?.includes('youtube.com/watch');

    const shouldSkipCaptcha = await checkShouldSkipCaptcha();

    if (
      !shouldSkipCaptcha &&
      activeTab?.id &&
      activeTab?.url &&
      isActiveTab &&
      isConnected &&
      isCaptchaUrl
    ) {
      // Wait for the page to be fully loaded
      await new Promise<void>((resolve) => {
        const checkLoadStatus = async () => {
          const tabStatus = await browser_api.tabs.get(activeTab.id!);
          if (tabStatus.status === 'complete') {
            resolve();
          } else {
            setTimeout(checkLoadStatus, 100);
          }
        };
        checkLoadStatus();
      });

      await browser_api.tabs.sendMessage<ShowVideoLoaderMessage>(activeTab.id, {
        action: Actions.ShowPageLoader,
      });

      const newUrl = await replaceTimecodeWithSavedTime(activeTab.url);

      console.warn('tab updated started', newUrl);

      await browser_api.tabs.update(activeTab.id, { url: newUrl });
    }

    // if the url does not include www we need to add it
    if (
      activeTab?.id === tabId &&
      activeTab.url?.startsWith('https://youtube.com') &&
      isConnected
    ) {
      const newUrl = activeTab.url.replace(
        '//youtube.com',
        '//www.youtube.com',
      );

      const urlWithTimecode = await replaceTimecodeWithSavedTime(newUrl);

      await browser_api.tabs.update(tabId, { url: urlWithTimecode });
    }

    if (
      activeTab?.id === tabId &&
      activeTab.url?.startsWith('https://youtu.be') &&
      isConnected
    ) {
      const prevURL = new URL(activeTab.url);
      const params = new URLSearchParams(prevURL.search);
      const newUrl = new URL(BASE_URL); // https://www.youtube.com/watch

      for (const [key, value] of params.entries()) {
        newUrl.searchParams.set(key, value);
      }

      newUrl.searchParams.set('v', activeTab.url.split('/').pop()!);
      newUrl.searchParams.set('feature', 'youtu.be');

      const urlWithTimecode = await replaceTimecodeWithSavedTime(
        newUrl.toString(),
      );

      await browser_api.tabs.update(tabId, { url: urlWithTimecode });
    }
  } catch (error) {
    console.error(error);
    alert('error');
  }
});

export async function processError(error: any) {
  await setLocalStorageData({
    [LocalStorageKey.ErrorMessage]: error?.message,
  });
}

// Proxy listeners

browser_api.proxy.settings.onChange.addListener(
  handleProxyControlledByOtherExtensions,
);

browser_api.proxy.settings.get({}).then(handleProxyControlledByOtherExtensions);

initNetworkConnectionMonitor();
initIcon();
